import locale from '../../../locales/zh_CN';
import { LinkPlugin } from '@ali/4ever-bamboo';
import { cardFactory, CustomizedBiCardPluginType } from '@ali/4ever-factory';
import { BiPlugin, BiPluginConfig } from '../../../types';
import createRenderNode from './createRenderNode';
import { LinkPluginConfig } from '../types';
import careateOnAction from './onAction';
import * as actions from './actions';
import { copyLink } from './commands';

export { actions };

function createBiLinkCardPlugin(bizConfig: BiPluginConfig) {
  return cardFactory((bizConfig: BiPluginConfig) => {

    const linkCardConfig = {
      locale: { ...locale.link.locale },
      ...bizConfig.link
    } as LinkPluginConfig;

    const serializer = bizConfig.clipboard?.serializer;

    const renderNode: CustomizedBiCardPluginType['renderNode'] = createRenderNode(linkCardConfig, false);

    return {
      cardType: 'link',
      onAction: careateOnAction(linkCardConfig, serializer),
      renderNode,
      queries: {
        getMinTableColWidth: LinkPlugin.getMinTableColWidthForBlockLinkCard
      }
    };
  })(bizConfig) as any as BiPlugin
};

createBiLinkCardPlugin.actions = actions;
createBiLinkCardPlugin.utils = {
  copyLink
};


export default createBiLinkCardPlugin;
